import React from 'react';
const blank = require('../assets/img/like-blank.png')
const liked = require('../assets/img/like-active.png')

export default class PostAdd extends React.Component {

    state = {
        likeImgSrc: blank,
        isLiked: false
    }
    onLikeClicked = (post, loggedInUser, isLiked) => {
        let user = {
            _id: loggedInUser._id,
            nickName: loggedInUser.username,
            imgUrl: loggedInUser.imgUrl
        }
        if (this.state.likeImgSrc === blank) {
            this.setState({ likeImgSrc: liked })
            try {
                this.props.likeUnkikePost(post, user, isLiked)//
                this.setState({ isLiked: true })
            } catch (err) {
                console.log('onLikeClicked -> error in likePost', err)
            }

        }
        else {
            this.setState({ likeImgSrc: blank })
            this.setState({ isLiked: true })
            try {
                this.props.likeUnkikePost(post, user, isLiked)//
                this.setState({ isLiked: false })
            } catch (err) {
                console.log('onLikeClicked -> error in likePost', err)
            }
        }
    }

    // onLikeClicked = (post, loggedInUser, isLiked) => {
    //     if (this.state.likeImgSrc === blank) {
    //         this.setState({ likeImgSrc: liked })

    //         let likedBy = {
    //             _id: loggedInUser._id,
    //             nickName: loggedInUser.username,
    //             imgUrl: loggedInUser.imgUrl
    //         }
    //         try {
    //             this.props.likePost(post, likedBy, isLiked)//
    //             this.setState({ isLiked: true })
    //         } catch (err) {
    //             console.log('onLikeClicked -> error in likePost', err)
    //         }

    //     }
    //     else {
    //         this.setState({ likeImgSrc: blank })
    //         this.setState({ isLiked: true })
    //     }
    // }

    checkLikeState = (ev, post, loggedInUser) => {
        post.likedBy.forEach(user => {
            if (user._id === loggedInUser._id) {
                this.setState({ likeImgSrc: liked })
            }
        });
    }

    render() {
        const { post, loggedInUser } = this.props
        return (
            <img className="post-btn like-btn" alt="like" title="like" onLoad={(ev) => this.checkLikeState(ev, post, loggedInUser)}
                onClick={() => this.onLikeClicked(post, loggedInUser, this.state.isLiked)} src={this.state.likeImgSrc}>
            </img>
        );
    }
}