function _setPosts(posts) {
    return {
        type: 'SET_POSTS',
        posts: posts
    };
}
function _setPost(post) {
    return {
        type: 'SET_POST',
        selectedPost: post
    };
}
function _addPost(post) {
    return {
        type: 'POST_ADD',
        post
    };
}
function _removePost(postId) {
    return {
        type: 'POST_REMOVE',
        postId
    };
}
function _likePost(post) {
    return {
        type: 'POST_LIKE',
        post
    };
}
